<template>
  <div class="hello">
    <div class="h_head">
      <img src="../../static/image/logo-bg.jpg">
      <div class="bg">
        <div>
          <div class="bg_logo">
            <img src="../../static/image/login_log.png">
          </div>
          <ul class="bg_list">
            <li class="bg_item">
              <label>用户名</label>:
              <input type="text" placeholder="用户名" v-model="name">
            </li>
            <li class="bg_item">
              <label style="letter-spacing:7px;">密码</label>:
              <input type="password" placeholder="密码" v-model="password">
            </li>
            <li>
              <label style="width:150px;display:inline-block"></label>
              <input type="checkbox" id="for">
              <label for="for">记住密码</label>
            </li>
          </ul>
          <div class="login">
            <span @click="msg()">登陆</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {

  data() {
    return {
      name: '',
      password: '',
      success: '',
      ruleInline: {
        name: [
          { required: true, message: 'Please fill in the user name', trigger: 'blur' }
        ],
        password: [
          { required: true, message: 'Please fill in the password.', trigger: 'blur' },
          { type: 'string', min: 6, message: 'The password length cannot be less than 6 bits', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    msg(name) {

      var name = this.name,
        password = this.password;

      this.$axios.get('/api/front/member/login.json', {
        params: {
          account: name,
          password: password
        }
      }, {
        }).then(response => {
          this.success = response.data.success;
          if (this.success) {
            this.$Message.success('登陆成功');
            this.$router.push({
             path: '/Delivery',
             query:{
               name:this.name
             }
            }
              
            );
          
         
          } else if (this.success == "false") {
            this.$Message.error('账号或者密码错误!');
          }
        })



    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.h_head {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  overflow: hidden;
  min-width: 1200px;
}

.h_head img {
  width: 100%;
  height: 113%;
}

.bg {
  padding: 30px;
  width: 600px;
  height: 460px;
  position: relative;
  bottom: 690px;
  left: 27%;
  background-color: rgba(255, 251, 240, 0.5);
  box-shadow: rgba(0, 0, 100%, 0.5);
  border-radius: 10px;
}

.bg_logo {
  height: 55px;
  text-align: center;
  margin-bottom: 50px;
}

.bg_logo img {
  width: 241px;
}

.bg_list li:last-child {
  height: 50px;
  border-bottom: 1px dashed #fff;
}

.bg_item {
  text-align: center;
  margin-bottom: 20px;
}

.bg_item label {
  width: 70px;
  display: inline-block;
  text-align: right;
}

.bg_item input {
  height: 2.2em;
  width: 319px;
  border: none;
  border: 1px solid #ccc;
  border-radius: 5px;
  -web-kit-appearance: none;
  -moz-appearance: none;
  outline: 0;
  text-decoration: none;
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}

.bg_item input:focus {
  border-color: #66afe9;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(102, 175, 233, 0.6);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(102, 175, 233, 0.6);
}

.login span {
  text-align: center;
  width: 233px;
  height: 40px;
  display: block;
  line-height: 40px;
  margin: 0 auto;
  background-color: #4876FF;
  border-radius: 5px;
  color: #fff;
  margin-top: 40px;
  cursor: pointer;
}
</style>



