<template>
    <div>
        <div class="d_head">
            <div class="date">
                <Input style="width:120px;" placeholder='条形码' v-model="formItem.shopName"></Input>
                <DatePicker transfer="true" type="datetimerange" format="yyyy-MM-dd HH:mm" :editable="false" placeholder="Select date and time(Excluding seconds)" style="width: 300px"></DatePicker>
                <Button type="success">查询</Button>
            </div>

            <ul class="de_list">
                <li class="de_item">
                    <Button type="info">今天</Button>
                </li>
                <li class="de_item">
                    <Button type="info">昨天</Button>
                </li>
                <li class="de_item">
                    <Button type="info">一周</Button>
                </li>
                <li class="de_item">
                    <Button type="info">半月</Button>
                </li>
                <li class="de_item">
                    <Button type="info">一月</Button>
                </li>
                <li class="de_item">
                    <Button type="info">半年</Button>
                </li>
                <li class="de_item">
                    <Button type="info">一年</Button>
                </li>
                <li class="de_item">
                    <label style="cursor:pointer;color:#3696fd;">清空筛选</label>
                </li>
            </ul>
        </div>
        <div>
            <div class="li">
                <div style="width:50%;">
                    <Button type="info">返回供应商</Button>
                </div>
                <div style="float:right;width:50%;text-align:right;">
                    <Button type="primary" @click="show">添加商品</Button>
                </div>
            </div>
            <div style="margin-top:10px;">
                <Table :loading="loading" border :columns="columns1" :data="data1"></Table>
            </div>
        </div>
        <Page :total="this.page" size="small" show-elevator show-sizer style="margin-top:30px;"></Page>
        <div class="v-transfer-dom" v-if="this.isA==true">
            <div class="ivu-modal-mask"></div>
            <div class="ivu-modal-wrap ">

                <Form :model="formItem" :label-width="80" class="from">
                    <div style="background:#ccc;font-size:16px;">商品
                        <div @click="close" style="float:right;margin:5px auto; cursor: pointer;">
                            <Icon type="ios-close"></Icon>
                        </div>
                    </div>
                    <hidden v-model="formItem.id">
                    </hidden>
                    <Row>
                        <Col :span="11">
                        <FormItem label="商品条码">
                            <Input v-model="formItem.code"></Input>
                        </FormItem>
                        </Col>
                        <Col :span="11">
                        <FormItem label="商品名称">
                            <Input v-model="formItem.name"></Input>
                        </FormItem>
                        </Col>
                        <Col :span="11">
                        <FormItem label="进价" style="width:300px;">
                            <Input v-model="formItem.price"></Input>
                        </FormItem>
                        </Col>
                    </Row>
                    <FormItem label="单位" style="width:300px;">
                        <Select v-model="formItem.companyEnum">
                            <Option v-for="item in data2" :value="item.name" :key="item.name">{{item.message}}</Option>
                        </Select>
                    </FormItem>

                    <FormItem label="分类">
                        <Select v-model="formItem.oneType" @on-change="onChage(formItem.oneType)">
                            <Option v-for="item in types" :value="item.id" :key="item.id">{{item.name}}</Option>
                        </Select>
                    </FormItem>
                    <FormItem>
                        <Select v-model="formItem.twoType" @on-change="onChage2(formItem.twoType)">
                            <Option v-for="item in data3" :value="item.id" :key="item.id">{{item.name}}</Option>
                        </Select>
                    </FormItem>
                    <FormItem>
                        <Select v-model="formItem.threeType">
                            <Option v-for="item in data4" :value="item.id" :key="item.id">{{item.name}}</Option>
                        </Select>
                    </FormItem>
                    <Row>
                        <Col span="11">
                        <FormItem label="供货等级I">
                            <Input v-model="formItem.supplierOneLevel"></Input>
                        </FormItem>
                        </Col>
                        <Col span="11">
                        <FormItem label="供货等级II">
                            <Input v-model="formItem.supplierTwoLevel"></Input>
                        </FormItem>
                        </Col>
                        <Col span="11">
                        <FormItem label="供货等级III">
                            <Input v-model="formItem.supplierThreeLevel"></Input>
                        </FormItem>
                        </Col>
                        <Col span="11">
                        <FormItem label="供货等级IV">
                            <Input v-model="formItem.supplierFourLevel"></Input>
                        </FormItem>
                        </Col>
                    </Row>
                    <FormItem style="margin-left:150px;">
                        <Button @click="sumbit" type="success">提交</Button>
                    </FormItem>
                </Form>
            </div>

        </div>

    </div>
</template>

<script>
export default {
    data() {
        return {
            isA: false,
            formItem: {
                code: '',
                name: '',
                price: '',
                message: '',
                oneType: '',
                twoType: '',
                threeType: '',
                supplierOneLevel: '',
                twoLesupplierTwoLevelvel: '',
                supplierThreeLevel: '',
                supplierFourLevel: '',
                id: ''
            },
            columns1: [
                {
                    title: '商品条码',
                    key: 'code'
                },
                {
                    title: '商品名',
                    key: 'name'
                },
                {
                    title: '进价',
                    key: 'price'
                },
                {
                    title: '时间',
                    key: 'rawAddTime',
                    render: (h, params) => {
                        var da = params.row.rawAddTime;
                        da = new Date(da);
                        var year = da.getFullYear() + '年';
                        var month = da.getMonth() + 1 + '月';
                        var date = da.getDate() + '日';
                        var time = [year, month, date].join('-');
                        return time;
                    }
                },
                {
                    title: '操作',
                    key: 'address',
                    align: 'center',
                    render: (h, params) => {
                        return h('div', [
                            h('Button', {
                                props: {
                                    type: 'info'
                                },
                                on: {
                                    click: () => {
                                        console.log(params);
                                        this.show(params.row.id);
                                    }
                                }
                            }, "修改")
                        ])
                    }
                }
            ],
            data1: [
                {
                    id: '',
                    code: '',
                    name: '',
                    address: '',
                    rawAddTime: '',
                    price: '',
                    level: ''
                }
            ],
            data2: '',
            loading: true,
            page: '',
            types: '',
            data3: '',
            data4: '',
            id: ''
        }
    },
    mounted() {
        this.$axios.get('/api/front/commodity/findCommodityPage.json', {}, {

        }).then(response => {
            var find = [];
            find = response.data.data.content;
            this.page = find.length;
            this.data1 = find;
            this.loading = false;

        }),
            //单位接口
            this.$axios.get('/api/metadata/getByEnumClassSimpleName.json?', {
                params: {
                    enumClassSimpleName: 'CompanyEnum'
                }

            }, {

                }).then(response => {
                    let selec = response.data.data;
                    this.data2 = selec;

                }),
            this.$axios.get('/api/front/commodity/findCommodityType.json', {
                params: {
                    id: -1
                }
            }, {}).then(response => {
                let unit = response.data.data;
                this.types = unit;
            })
    },
    methods: {
        sumbit() {
            this.$axios.post('/api/front/commodity/create.json', {
                code: this.formItem.code,
                name: this.formItem.name,
                price: this.formItem.price,
                companyEnum: this.formItem.companyEnum,
                oneType: this.formItem.oneType,
                twoType: this.formItem.twoType,
                threeType: this.formItem.threeType,
                supplierOneLevel: this.formItem.supplierOneLevel,
                supplierTwoLevel: this.formItem.supplierTwoLevel,
                supplierThreeLevel: this.formItem.supplierThreeLevel,
                supplierFourLevel: this.formItem.supplierFourLevel
            }, {
                    header: {
                        "Content-Type": "application/json;charset=utf-8"
                    }
                }).then(response => {
                    if (response.data.success) {
                        this.$Message.info({
                            content: response.data.message,
                            duration: 10,
                            closable: true
                        });
                        this.isA == false ? this.isA = true : this.isA = false;
                    } else {
                        this.$Message.info({
                            content: response.data.message,
                            duration: 10,
                            closable: true
                        });
                    }
                })

        },
        show(id) {
            this.isA == false ? this.isA = true : this.isA = false;
            this.$axios.get('/api/front/commodity/findById.json', {
                params: {
                    id: id
                }

            }).then(response => {
                var data = response.data.data;
                console.log(data);
                this.formItem.code = data.code,
                    this.formItem.name = data.name,
                    this.formItem.price = data.price,
                    this.formItem.companyEnum = data.companyEnum.message,
                    this.formItem.oneType = data.oneType,
                    this.formItem.twoType = data.twoType,
                    this.formItem.threeType = data.threeType,
                    this.formItem.supplierOneLevel = data.supplierOneLevel,
                    this.formItem.supplierTwoLevel = data.supplierTwoLevel,
                    this.formItem.supplierThreeLevel = data.supplierThreeLevel,
                    this.formItem.supplierFourLevel = data.supplierFourLevel;
                this.formItem.code = data.code;
            })
        },
        changeApi(id, type) {
            this.$axios.get('/api/front/commodity/findCommodityType.json', {
                params: {
                    id: id
                }
            }, {}).then(response => {
                if (type == 1) {
                    this.data3 = response.data.data;
                } else if (type == 2) {
                    this.data4 = response.data.data;
                }
            })
        },
        onChage(ids) {
            this.changeApi(ids, 1);
            this.id = ids;

        },
        onChage2(id) {
            this.changeApi(id, 2);
            this.id = id;
        },
        close(){
            this.isA == false ? this.isA = true : this.isA = false;
        }
    }

}
</script>

<style>
.li {
    margin-top: 20px;
    display: flex;
}

.ivu-icon {
    cursor: pointer;
}

.from {
    width: 500px;
    margin-top: 50px;
    border: 1px solid #ccc;
    z-index: 9999;
    margin-left: 30%;
    background: #fff;
}
</style>




