            <Select v-model="model3" style="width:100px" :value="this.cList[0].value">
                <Option v-for="item in cList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
        </div>
        <Table :loading="loading" border :columns="columns1" :data="data1"></Table>
        <Page :total="pageNum" @on-page-size-change="sss(pagesize)" :page-size="pagesize" size="samll" show-total show-sizer show-elevator style="margin-top:30px;"></Page>
        <div class="v-transfer-dom" v-if="this.tableHide==true">
