                                                        <div style="background:#ccc;font-size:16px;">调整订单信息
                                                            <div @click="icon()" style="float:right;margin:5px auto; cursor: pointer;">
                                                                <Icon type="ios-close"></Icon>
                                                            </div>

                                                        </div>
                                                        <ul class="hide_list">
                                                            <li class="hide_item">
                                                                <span>采购单号:</span>
                                                                <label>{{this.tableData.orderNo}}</label>
                                                            </li>
                                                            <li class="hide_item">
                                                                <span>采购店铺:</span>
                                                                <label>{{this.tableData.orderShop}}</label>
                                                            </li>
                                                            <li class="hide_item">
                                                                <span>店铺地址:</span>
                                                                <label>{{this.tableData.shopAddress}}</label>
                                                            </li>
                                                            <li class="hide_item">
                                                                <span>店铺电话:</span>
                                                                <label>{{this.tableData.shopPhone}}</label>
                                                            </li>
                                                            <li class="hide_item">
                                                                <span>状态:</span>
                                                                <label>{{this.sta}}</label>
                                                            </li>
                                                        </ul>
                                                        <table border="1" cellspacing="0" class="tables">
                                                            <tr>
                                                                <th>商品条码</th>
