                                                                <th>赠送数量</th>
                                                                <th>供应商进价</th>
                                                                <th>店铺进价</th>
                                                                <th>供应等级</th>
                                                            </tr>
                                                            <tr v-for="item in this.tableData2" :key="item.id">
                                                                <td><input type="text" :value="item.barCode" class="table_input" readonly="readonly"></input>
                                                                </td>
                                                                <td><input type="text" :value="item.name" class="table_input" readonly="readonly"></input>
                                                                </td>
                                                                <td><input type="text" :value="item.number" class="table_input" readonly="readonly"></input>
                                                                </td>
                                                                <td><input type="text" :value="item.number" class="table_input" readonly="readonly"></input>
                                                                </td>
                                                                <td><input type="text" :value="item.supplierPrice" class="table_input" style="background:#fff;"></input>
                                                                </td>
                                                                <td><input type="text" :value="item.shopPrice" class="table_input" readonly="readonly"></input>
                                                                </td>
                                                                <td>
                                                                    <Select v-model="model3" style="width:100px">
                                                                        <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                                                                    </Select>
                                                
                                                                </td>
                                                            </tr>

                                                        </table>
                                                    </div>
                                                </div>-->

        <div class="v-transfer-dom" v-if="this.tableHide2==true">
            <div class="ivu-modal-mask"></div>
            <div class="ivu-modal-wrap ">
                <div class="hide_div">

                    <div style="background:#ccc;font-size:16px;">填写送货人
                        <div @click="delivery" style="float:right;margin:5px auto; cursor: pointer;">
                            <Icon type="ios-close"></Icon>
                        </div>
                    </div>
                    <table border="1" cellspacing="0" class="tables">
                        <tr>
                            <th>送货人</th>
                            <th>送货人电话</th>
                        </tr>
                        <tr>
                            <td>
                                <Input style="width:220px;" v-model="deliverName" placeholder='送货人名字'></Input>
                            </td>
                            <td>
                                <Input style="width:220px;" v-model="deliverPhone" placeholder='送货人电话'></Input>
                            </td>
                        </tr>
                    </table>
                    <Button @click="delivery1" type="success" style="margin-left:450px">点击送货</Button>
                </div>

            </div>

        </div>
    </div>
</template>



<script>
export default {
    data() {
        return {
            cList: [
                {
                    value: '请选择',
                    label: '请选择'
                },
                {
                    value: '待送货',
                    label: '待送货'
                },
                {
                    value: '已完成',
                    label: '已完成'
                },
                {
                    value: '送货中',
                    label: '送货中'
                }
            ],
            columns1: [
                {
                    title: '下单号',
                    key: 'name'
                },
                {
                    title: '下单时间',
                    key: 'date',
                    width: 180
                },
                {
                    title: '采购店铺',
                    key: 'shop'
                },
                {
                    title: '店铺地址',
                    key: 'address'
                },
                {
                    title: '店铺电话',
                    key: 'phone'
                },
                {
                    title: '订单状态',
                    key: 'sta',
                    align: 'center',
                    render: (h, params) => {
                        return h('div', [
                            h('span', {

                                class: this.isA ? 's_span' : 's_spa'

                            }, this.sta)
                        ])
                    }
                },
                {
                    title: '操作',
                    width: 300,
                    align: 'center',
                    render: (h, params) => {
                        return h('div', [
                            h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.sho(params.index);
                                    }
                                }
                            }, "查看详情"),
                            h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        this.delivery(params.index)
                                    }
                                }
                            }, "点击开始送货")
                        ])
                    }
                }
            ],
            data1: [
                {
                    name: '',
                    age: 0,
                    address: '',
                    date: '',
                    shop: '',
                    phone: '',
                    sta: ''
                }
            ],
            loading: true,
            tableHide: false,
            sta: '',
            isA: true,
            tableData: '',
            model3: '',
            tableData2: '',
            tableHide1: false,
            pagesize: 10,
            pageNum: 0,
            tableHide2: false,
            id: "",
            deliverName: '',
            deliverPhone: ''

        }
    },
    mounted() {

        this.$axios.get("/api/front/order/findPageOrder.json", {}, {
        }).then(response => {
            var cont = '';
            cont = response.data.data.content;
            this.pageNum = cont.length;
            for (var i = 0; i < cont.length; i++) {
                this.data1[i].name = cont[i].orderNo;
                this.id = cont[i].id;
                var newTime = new Date(cont[i].orderTime),
                    oYear = newTime.getFullYear(),
                    oMonth = newTime.getMonth() + 1,
                    oDay = newTime.getDate(),
                    oHour = newTime.getHours(),
                    oMin = newTime.getMinutes(),
                    oSen = newTime.getSeconds(),
                    oTime = oYear + '-' + oMonth + '-' + oDay + ' ' + oHour + ':' + oMin + ':' + oSen;
                this.data1[i].date = oTime;
                this.data1[i].address = cont[i].shopAddress;
                this.data1[i].shop = cont[i].orderShop;
                this.data1[i].phone = cont[i].shopPhone;
                this.sta = cont[i].orderStatusEnum.message
                this.loading = false;
                this.sta == "待发货" ? this.isA = true : this.isA = false;
            }

        })
    },
    methods: {
        sho(index) {
            this.tableHide == false ? this.tableHide = true : this.tableHide = false;

            this.$axios.get('/api/front/order/findById.json', {
                params: {
                    id: this.id
                }
            }).then(response => {
                this.tableData = response.data.data;

            })
        },
        icon(index) {
            this.tableHide1 == false ? this.tableHide1 = true : this.tableHide1 = false;
        },
        update() {
            this.$axios.get('/api/front/commodity/update.json', {
            }, {

                }).then(response => {
                    thia.tableData2 = response.data.data;
                })
        },
        sss(v) {
    
            alert(v);
        },
        delivery() {

            this.tableHide2 == false ? this.tableHide2 = true : this.tableHide2 = false;
        },
        delivery1(index) {

            let name = this.deliverName,
                phone = this.deliverPhone;
            if (name == "" && phone == "") {
                this.$Message.info({
                    content: '请输入送货人和电话',
                    duration: 10,
                    closable: true
                });
            } else {
                this.$axios.post('/api/front/order/orderDelivery.json', {
                    id: this.id,
                    deliveryMan: this.deliverName,
                    deliveryManPhone: this.deliverPhone
                }, {
                     header: {
                        "Content-Type": "application/json;charset=utf-8"
                    }
                }).then(response => {
                        if (response.data.success) {
                           
                            this.$Message.info({
                                content: '成功',
                                duration: 10,
                                closable: true
                            });
                            this.tableHide2 == false ? this.tableHide2 = true : this.tableHide2 = false;
                        } else {
                            this.$Message.info({
                                content: '失败',
                                duration: 10,
                                closable: true
                            });
                            
                        }
                    })
            }


        }
    }
}
</script>






